import { useState, useEffect } from 'react';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import {
	Table,
	TableBody,
	TableCell,
	TableContainer,
	TableRow,
	Alert,
	useTheme,
	IconButton,
	Grid,
} from '@material-ui/core';
import EditTwoToneIcon from '@material-ui/icons/EditTwoTone';
import { makeStyles } from '@material-ui/core/styles';
import { useQuery, gql, useLazyQuery } from '@apollo/client';
import { useHistory } from 'react-router';
import { generatePath } from 'react-router-dom';
import { TProperty } from '../../../types';
import graphql from '../../../graphql';
import Loader from '../../../ui-component/Loader';
import Header from './ListHeader';
import LoadMore from '../components/LoadMore';
import config from '../../../config';
import PropertyToolbar from './PropertyToolbar';
import { formatDate } from '../../../helpers/date';
import { getValidSearchQuery } from '../../../helpers/searchFilter';
import { extractPropertyData } from './Helper';
import { PATHS } from '../../../routes/path';

const useStyles = makeStyles(() => ({
	table: {
		minWidth: '750px',
	},
}));

const PropertyList = () => {
	const theme = useTheme();
	const classes = useStyles();
	const history = useHistory();

	const { loading, error, data, fetchMore } = useQuery(
		gql(graphql.getProperties),
		{
			variables: { PageIndex: 0, PageSize: config.pagination.limit },
			fetchPolicy: 'cache-first',
			notifyOnNetworkStatusChange: true,
		}
	);

	const {
		loading: loadingStatuses,
		error: statusesError,
		data: statusesData,
	} = useQuery(gql(graphql.listPropertyStatuses));

	const [doSearch, setDoSearch] = useState(false);
	const [search, setSearch] = useState('');
	const [selectedStatuses, setSelectedStatuses] = useState([]);
	const [executeSearch, { loading: isSearching, data: searchData }] =
		useLazyQuery(gql(graphql.getProperties));

	const [rows, setRows] = useState<TProperty[]>([]);
	const [hasMore, setHasMore] = useState<boolean>(false);
	const [page, setPage] = useState<number>(0);

	const getData = () => {
		if (isSearching) return {};
		if (doSearch) return searchData;
		return data;
	};

	useEffect(() => {
		const newData = getData();
		const newCount = _get(newData, 'getProperties.Count', 0);
		const newTotal = _get(newData, 'getProperties.Total', 0);
		setHasMore(newCount < newTotal);
		setPage(_get(newData, 'getProperties.PageIndex') || 0);
		setRows(_get(newData, 'getProperties.Items') || []);
	}, [data, searchData, doSearch, isSearching]);

	const isLoading = loading || isSearching;

	if ((isLoading || loadingStatuses) && !data) return <Loader />;

	if (error || statusesError) {
		return (
			<Alert severity='error' sx={{ color: theme.palette.error.main }}>
				Something went wrong!
			</Alert>
		);
	}

	const onCreateHandler = () => {
		const propertyAddNewLink = generatePath(PATHS.PROPERTY_ADD);
		history.push(propertyAddNewLink);
	};

	const editProperty = (id: string) => {
		history.push(`/property/${id}`);
	};

	const searchHandler = (pageIndex = 0) => {
		const { query, statusIds } = getValidSearchQuery({
			search,
			selectedStatuses,
		});
		if (query) {
			executeSearch({
				variables: {
					PageIndex: pageIndex,
					PageSize: config.pagination.limit,
					Query: query,
					StatusIds: statusIds,
				},
			});
		}
		setDoSearch(Boolean(query));
	};

	const loadMore = () => {
		if (doSearch) {
			searchHandler(page + 1);
			return;
		}
		fetchMore({
			variables: { PageIndex: page + 1, PageSize: config.pagination.limit },
		});
	};

	const statuses = _get(statusesData, ['listPropertyStatuses'], []);

	return (
		<>
			{isSearching && <Loader />}
			<PropertyToolbar
				onCreate={onCreateHandler}
				onSearch={searchHandler}
				onTriggerSearch={setDoSearch}
				statuses={statuses}
				search={search}
				setSearch={setSearch}
				selectedStatuses={selectedStatuses}
				setSelectedStatuses={setSelectedStatuses}
			/>
			<TableContainer>
				<Table className={classes.table} aria-labelledby='tableTitle' size='small'>
					<Header />
					<TableBody>
						{rows.map(row => {
							if (typeof row === 'number') return null;
							const {
								streetAddress,
								county,
								zip,
								city,
								state,
								propertyNo,
								createdAt,
							} = extractPropertyData(row);
							return (
								<TableRow hover tabIndex={-1} key={row.PropertyId}>
									<TableCell align='left'>{propertyNo}</TableCell>
									<TableCell align='left'>{streetAddress}</TableCell>
									<TableCell align='left'>{county}</TableCell>
									<TableCell align='left'>{state}</TableCell>
									<TableCell align='left'>{zip}</TableCell>
									<TableCell align='left'>{city}</TableCell>
									<TableCell align='left'>{formatDate(createdAt)}</TableCell>
									<TableCell align='center' sx={{ pr: 3 }}>
										<IconButton
											color='primary'
											onClick={() => editProperty(row.PropertyId)}
										>
											<EditTwoToneIcon sx={{ fontSize: '1.3rem' }} />
										</IconButton>
									</TableCell>
								</TableRow>
							);
						})}
					</TableBody>
				</Table>
			</TableContainer>
			{_isEmpty(rows) && !isSearching && (
				<Grid item xs={12}>
					<Alert icon={false} severity='info'>
						No Records found
					</Alert>
				</Grid>
			)}
			{hasMore && <LoadMore onLoadMore={loadMore} loading={isLoading} />}
		</>
	);
};

export default PropertyList;
